*{
  margin:0;
}

.header{
  background-color:#c6c6c6;
  
}
.headerName{
  float: right;
  padding: 20px;
  font-weight: bold;
  font-size: 20px;
}
.backgroundProp{
  height:100vh;
   
    display: grid;
    grid-template-columns: 40% 60%;
}

.croppeimageproperties {
  width: 45vh; /* width of container */
  height: 50vh; /* height of container */
  object-fit: cover;
  object-position: 20% 10%; /* try 20px 10px */ 
}

.headerRight{
  font-size: 25px;
  display: flex;
  margin: auto;
}

h1{
  animation: type 3s steps(22);
  overflow: hidden;
  white-space: nowrap;
  font-family: consolas;
  border-right: 4px solid black;
  width: 22ch;
}
@keyframes type{
  0%{width: 0ch;}
  100%{
    width:22ch ;
  }
}